import { DashboardWrapper } from "@/components/layout/dashboard-wrapper";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Search, MessageSquare, Calendar, ChevronDown, ChevronUp, Star, TrendingUp } from "lucide-react";
import Link from "next/link";
import { useState } from "react";

// Mock data for demonstration
const mockStudents = [
  {
    id: "1",
    name: "John Doe",
    email: "john.doe@example.com",
    avatar: "/avatars/john-doe.png",
    subjects: ["Mathematics", "Physics"],
    sessionsCompleted: 12,
    upcomingSessions: 2,
    averageRating: 4.8,
    lastSession: "2024-03-15",
    status: "active",
    progress: 85,
    attendanceRate: 95,
    performance: [
      { subject: "Mathematics", score: 92 },
      { subject: "Physics", score: 88 }
    ],
    sessionHistory: [
      { date: "2024-03-15", duration: 60, topic: "Calculus" },
      { date: "2024-03-12", duration: 90, topic: "Mechanics" }
    ]
  },
  {
    id: "2",
    name: "Jane Smith",
    email: "jane.smith@example.com",
    avatar: "/avatars/jane-smith.png",
    subjects: ["Chemistry", "Biology"],
    sessionsCompleted: 8,
    upcomingSessions: 1,
    averageRating: 4.9,
    lastSession: "2024-03-14",
    status: "active",
    progress: 78,
    attendanceRate: 100,
    performance: [
      { subject: "Chemistry", score: 85 },
      { subject: "Biology", score: 90 }
    ],
    sessionHistory: [
      { date: "2024-03-14", duration: 60, topic: "Organic Chemistry" },
      { date: "2024-03-10", duration: 90, topic: "Cell Biology" }
    ]
  }
];

export default function TutorStudentsPage() {
  const [expandedStudents, setExpandedStudents] = useState<Set<string>>(new Set());

  const toggleStudent = (studentId: string) => {
    const newExpanded = new Set(expandedStudents);
    if (newExpanded.has(studentId)) {
      newExpanded.delete(studentId);
    } else {
      newExpanded.add(studentId);
    }
    setExpandedStudents(newExpanded);
  };

  return (
    <DashboardWrapper>
      <div className="container mx-auto py-8">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold">Students</h1>
          <div className="flex gap-2">
            <div className="relative">
              <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input placeholder="Search students..." className="pl-8" />
            </div>
            <Button asChild>
              <Link href="/dashboard/tutor/students/invite">
                Invite Student
              </Link>
            </Button>
          </div>
        </div>

        <Card>
          <CardContent className="p-0">
            <div className="divide-y">
              {mockStudents.map((student) => (
                <div key={student.id}>
                  <div 
                    className="flex items-center justify-between p-4 hover:bg-muted/50 cursor-pointer"
                    onClick={() => toggleStudent(student.id)}
                  >
                    <div className="flex items-center space-x-4">
                      <Avatar>
                        <AvatarImage src={student.avatar} />
                        <AvatarFallback>{student.name.split(" ").map(n => n[0]).join("")}</AvatarFallback>
                      </Avatar>
                      <div>
                        <h3 className="font-semibold">{student.name}</h3>
                        <p className="text-sm text-muted-foreground">{student.email}</p>
                      </div>
                    </div>
                    <div className="flex items-center space-x-4">
                      <div className="flex items-center space-x-2">
                        <TrendingUp className="h-4 w-4 text-muted-foreground" />
                        <span className="text-sm">{student.progress}%</span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <Star className="h-4 w-4 text-muted-foreground" />
                        <span className="text-sm">{student.averageRating}</span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <Calendar className="h-4 w-4 text-muted-foreground" />
                        <span className="text-sm">{student.upcomingSessions} upcoming</span>
                      </div>
                      {expandedStudents.has(student.id) ? (
                        <ChevronUp className="h-4 w-4 text-muted-foreground" />
                      ) : (
                        <ChevronDown className="h-4 w-4 text-muted-foreground" />
                      )}
                    </div>
                  </div>
                  
                  {expandedStudents.has(student.id) && (
                    <div className="p-4 bg-muted/50">
                      <div className="grid grid-cols-2 gap-4 mb-4">
                        <div>
                          <h4 className="font-semibold mb-2">Subjects</h4>
                          <div className="flex flex-wrap gap-2">
                            {student.subjects.map((subject) => (
                              <Badge key={subject} variant="secondary">
                                {subject}
                              </Badge>
                            ))}
                          </div>
                        </div>
                        <div>
                          <h4 className="font-semibold mb-2">Quick Actions</h4>
                          <div className="flex gap-2">
                            <Button variant="outline" size="sm" asChild>
                              <Link href={`/dashboard/tutor/students/${student.id}/chat`}>
                                <MessageSquare className="h-4 w-4 mr-2" />
                                Message
                              </Link>
                            </Button>
                            <Button variant="outline" size="sm" asChild>
                              <Link href={`/dashboard/tutor/students/${student.id}/schedule`}>
                                <Calendar className="h-4 w-4 mr-2" />
                                Schedule
                              </Link>
                            </Button>
                          </div>
                        </div>
                      </div>
                      <div className="flex justify-end">
                        <Button variant="outline" size="sm" asChild>
                          <Link href={`/dashboard/tutor/students/${student.id}`}>
                            View Full Profile
                          </Link>
                        </Button>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </DashboardWrapper>
  );
} 